.RichEditor-editor {
  cursor: text;
  color: #000;
  font-size: 16px;
  height: 100%;
  width: 100%;
  overflow: auto; }

.RichEditor-editor .RichEditor-blockquote {
  border-left: 5px solid #eee;
  color: #666;
  font-family: "Hoefler Text", "Georgia", serif;
  font-style: italic;
  margin: 16px 0;
  padding: 10px 20px; }

.RichEditor-text-align-center {
  text-align: center !important; }
  .RichEditor-text-align-center > div {
    display: inline; }

.RichEditor-text-align-left {
  text-align: left !important; }
  .RichEditor-text-align-left > div {
    display: inline; }

.RichEditor-text-align-right {
  text-align: right !important; }
  .RichEditor-text-align-right > div {
    display: inline; }

.RichEditor-text-align-justify {
  text-align: justify !important; }
  .RichEditor-text-align-justify > div {
    display: inline; }

.RichEditor-float-left {
  float: left !important;
  margin: 0; }

.RichEditor-float-right {
  float: right !important;
  margin: 0; }

.RichEditor-float-wrap {
  padding: 4px; }
  .RichEditor-float-wrap .RichEditor-img {
    cursor: pointer;
    width: 100%; }

.RichEditor-media {
  margin: 0; }

.RichEditor-iframe {
  display: inline-block;
  height: 18px;
  vertical-align: text-top; }
  .RichEditor-iframe iframe {
    border: none;
    height: 100%;
    width: 100%; }

